<template>
  <div>
    <div style="margin-bottom: 5px">
      <el-input
        v-model.trim="name"
        placeholder="请输入仓库名"
        :suffix-icon="ElIconSearch"
        style="width: 200px"
        @keyup.enter="loadPost"
      ></el-input>
      <el-button type="primary" style="margin-left: 5px" @click="loadPost"
        >查询</el-button
      >
      <el-button type="success" @click="resetParam">重置</el-button>

      <el-button type="primary" style="margin-left: 5px" @click="add"
        >新增</el-button
      >
    </div>
    <el-table
      :data="tableData"
      :header-cell-style="{ background: '#f2f5fc', color: '#555555' }"
      border
    >
      <el-table-column prop="id" label="ID" width="60"> </el-table-column>
      <el-table-column prop="name" label="仓库名" width="180">
      </el-table-column>
      <el-table-column prop="address" label="地址"> </el-table-column>
      <el-table-column prop="remark" label="备注"> </el-table-column>
      <el-table-column prop="operate" label="操作" width="200">
        <template v-slot="scope">
          <el-button size="small" type="success" @click="mod(scope.row)"
            >编辑</el-button
          >
          <el-popconfirm
            title="确定删除吗？"
            @confirm="del(scope.row.id)"
            style="margin-left: 5px"
          >
            <template v-slot:reference>
              <el-button size="small" type="danger">删除</el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageNum"
      :page-sizes="[5, 10, 20, 30]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>

    <el-dialog
      title="提示"
      v-model="centerDialogVisible"
      width="30%"
      center
    >
      <el-form ref="forms" :rules="rules" :model="form" label-width="80px">
        <el-form-item label="仓库名" prop="name">
          <el-col :span="20">
            <el-input v-model.trim="form.name"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="地址" prop="address">
          <el-col :span="20">
            <el-input v-model.trim="form.address"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-col :span="20">
            <el-input type="textarea" v-model.trim="form.remark"></el-input>
          </el-col>
        </el-form-item>
      </el-form>
      <template v-slot:footer>
        <span class="dialog-footer">
          <el-button @click="centerDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="save">确 定</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { inject,onBeforeMount,ref,reactive,toRefs,nextTick} from 'vue';
import { ElMessage } from 'element-plus';
import { Search as ElIconSearch } from '@element-plus/icons-vue'
export default {
  setup(){
    const axios = inject('axios');
    const thisdata = reactive({
      tableData: [],
      pageSize: 10,
      pageNum: 1,
      total: 0,
      name: '',
      centerDialogVisible: false,
      form: {
        id: '',
        name: '',
        address: '',
        remark: '',
      },
      rules: {
        name: [{ required: true, message: '请输入仓库名', trigger: 'blur' }],
        address: [{ required: true, message: '请输入地址', trigger: 'blur' }],
      },
    })
    function resetForm() {
      Object.keys(thisdata.form).forEach(key => {
        thisdata.form[key] = ''
      })
    }
    function del(id) {
      console.log(id)

      axios
        .get('/goodstype/del?id=' + id)
        .then((res) => res.data)
        .then((res) => {
          console.log(res)
          if (res.code == 200) {
            ElMessage.success('操作成功！')
            loadPost()
          } else {
            ElMessage.error('操作失败！')
          }
        })
    }
    function mod(row) {
      console.log(row)

      thisdata.centerDialogVisible = true
      nextTick(() => {
        //赋值到表单
        thisdata.form.id = row.id
        thisdata.form.name = row.name
        thisdata.form.remark = row.remark
        thisdata.form.address = row.address
      })
    }
    function add() {
      thisdata.centerDialogVisible = true
      nextTick(() => {
        resetForm()
      })
    }
    function doSave() {
      axios
        .post('/storage/save', thisdata.form)
        .then((res) => res.data)
        .then((res) => {
          console.log(res)
          if (res.code == 200) {
            ElMessage.success('操作成功！')
            thisdata.centerDialogVisible = false
            loadPost()
            resetForm()
          } else {
            ElMessage.error('操作失败！')
          }
        })
    }
    function doMod() {
      axios
        .post('/storage/update', thisdata.form)
        .then((res) => res.data)
        .then((res) => {
          console.log(res)
          if (res.code == 200) {
            ElMessage.success('操作成功！')
            thisdata.centerDialogVisible = false
            loadPost()
            resetForm()
          } else {
            ElMessage.error('操作失败！')
          }
        })
      }
      const forms = ref(null);
    function save() {
      forms.value.validate(valid => {
        if (valid){
          if (thisdata.form.id) {
            doMod()
          } else {
            doSave()
          }
        } else {
          ElMessage.error('检查仓库地址正确性')
          return false
        }})
      }
    function handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
      thisdata.pageNum = 1
      thisdata.pageSize = val
      loadPost()
    }
    function handleCurrentChange(val) {
      console.log(`当前页: ${val}`)
      thisdata.pageNum = val
      loadPost()
    }
    function resetParam() {
      thisdata.name = ''
    }
    function loadPost() {
      axios
        .post('/storage/listPage', {
          pageSize: thisdata.pageSize,
          pageNum: thisdata.pageNum,
          param: {
            name: thisdata.name,
          },
        })
        .then((res) => res.data)
        .then((res) => {
          if (res.code == 200) {
            thisdata.tableData = res.data
            thisdata.total = res.total
          } else {
            alert('获取数据失败')
          }
        })
    }
  onBeforeMount(()=> {
    loadPost()
  })
  return {
    ...toRefs(thisdata),
    ElIconSearch,forms,
    del,mod,add,save,handleCurrentChange,handleSizeChange,resetParam,loadPost,
  }
  },
  name: 'StorageManage',
}
</script>
